.button {
    margin: 10px;
    background-color: #9da1b8;
    border: none;
    color: rgba(27, 37, 194, 0.82);
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 25px;
}

.button:disabled {
    border: 1px solid #999999;
    background-color: #ba4a4a;
    color: #666666;
}