.setter {
    width: 300px;
    height: 100px;
    border: blue solid 4px;
    border-radius: 10px;
    color: blue;
    background-color: grey;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 1px 1px;
    grid-template-areas: ". ." ". .";
}

.input {
    background-color: darkgrey;
    padding: 5px;
    margin: 10px 10px 10px 0;
}

.error {
    background-color: #901212;
}

.label {
    padding: 5px;
    margin: 10px 0 10px 10px;
}